.deals {
  padding-top: 40px;
  padding-bottom: 50px;

  font-size: 16px;
  line-height: 22px;

  background-color: #eeeeee;

  &__inner {
    display: flex;
    flex-direction: column;
  }

  &__heading {
    margin-bottom: 24px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    background-color: #ffffff;
  }

  &__list {
    order: 2;
    padding: 32px 16px 35px 16px;
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;

    font-size: 22px;
    line-height: 29px;
    font-weight: 800;
  }

  &__debt {
    margin: 0;
    margin-bottom: 11px;

    font-size: 20px;
    line-height: 26px;
    color: #0838f1;
  }

  &__details {
    width: 100%;
    margin-bottom: 12px;

    font-size: 16px;
    line-height: 22px;

    border-collapse: collapse;
  }

  &__caption, &__date {
    padding-top: 14px;
    padding-bottom: 6px;

    border-bottom: 1px solid #d3d3d5;
  }

  &__caption {
    font-weight: 500;
  }

  &__date {
    text-align: right;
  }

  &__period {
    margin: 0;
    margin-bottom: 17px;
  }

  &__creditors {
    margin: 0;
    margin-bottom: 20px;
  }

  &__box {
    display: flex;
  }

  &__preview {
    margin-right: 20px;
  }

  &__text {
    width: 250px;
    margin: 0;
  }

  &__link-file {
    padding-bottom: 1px;
    color: #0838f1;
    border-bottom: 1px solid #0838f1;
  }

  @media (min-width: 768px) {
    &__row {
      max-width: 980px;
    }

    &__list {
      width: 100%;
    }
  }
}
