*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  min-width: 320px;
  height: 100%;

  margin: 0;
  padding: 0;

  font-family: "HelveticaNeueCyr", sans-serif;
  font-size: 18px;
  line-height: 25px;
  font-weight: 500;
  color: #000000;
}

h1 {
  margin: 36px 0;
  font-size: 36px;
  line-height: 42px;
}

h2 {
  margin: 24px 0;
  font-size: 24px;
  line-height: 28px;
}

h3 {
  margin: 18px 0;
  font-size: 18px;
  line-height: 20px;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

a {
  text-decoration: none;
}

button {
  border: none;
}
