.faq {
  padding-top: 40px;
  padding-bottom: 50px;

  font-weight: 400;
  color: #f5f5f6;

  background-color: #2b2c30;

  &__heading {
    margin-bottom: 20px;

    color: #f5f5f6;
  }

  &__list {
    max-width: 640px;
    background-color: #3e4146;
  }

  &__item {
    position: relative;

    &--active {
      display: block;
    }

    &:last-child:after {
      background-image: none;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-image: url("../img/border-bottom.svg");
      background-repeat: repeat-x;
    }
  }

  &__question {
    position: relative;
    margin: 0;
    padding: 20px 46px 20px 16px;
    line-height: 25px;
    font-weight: 500;

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 10px;
      width: 20px;
      height: 20px;
      background-image: url("../img/arrow-right.svg");
      background-repeat: no-repeat;
      transform: translate(0, -50%);
    }

    &:hover {
      cursor: pointer;
    }

  }

  &__answer {
    display: none;
    margin: 0;
    padding: 0 46px 20px 16px;

    font-size: 16px;
    line-height: 23px;
  }
}

.show {
  display: block;
}
