.steps {
  padding-bottom: 50px;

  font-size: 20px;
  line-height: 28px;

  &__heading {
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
  }

  &__sublist {
    margin: 0;
    margin-bottom: 25px;
    padding-left: 22px;

    &--last {
      margin-bottom: 0;
    }
  }

  &__subitem {
    margin-bottom: 14px;
  }

  @media (min-width: 768px) {
    &__list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }

    &__title {
      font-size: 22px;
      line-height: 31px;
      margin-bottom: 15px;
    }

    &__item {
      width: 30%;
      font-size: 18px;
      line-height: 25px;
    }
  }

}
