.list {
  margin: 0;
  padding: 0;

  list-style: none;

  &__item {
    position: relative;
    margin-bottom: 11px;
    padding-left: 36px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url("../img/checked.svg");
    }
  }
}
