.advantages {
  padding-bottom: 50px;

  &__inner {
    padding-top: 284px;

    background-image: url("../img/background-services-mobile@1x.png");
    background-repeat: no-repeat;
    background-position: top 30px center;
  }

  &__heading {
    margin-bottom: 17px;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    position: relative;
    margin-bottom: 11px;
    padding-left: 36px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      background-color: #2bba53;
    }
  }

  @media (min-width: 768px) {
    & {
      padding-bottom: 77px;
    }

    &__inner {
      padding-top: 40px;
      padding-left: 350px;

      background-image: url("../img/background-services-tablet@1x.png");
      background-position: center left 24px;
    }
  }
}
