.form {

  &__label {
    display: block;
    margin-bottom: 10px;

    font-size: 20px;
    line-height: 29px;
    color: #d3d3d5;

    &-privacy {
      position: relative;
      margin-bottom: 40px;
      padding-left: 38px;

      font-size: 16px;
      line-height: 23px;
    }
  }

  &__field {
    display: block;
    width: 213px;
    padding: 10px 25px 12px 10px;

    font-size: 22px;
    line-height: 31px;
    color: #000000;

    border: none;
    border-radius: 2px;
    background-color: #ffffff;

    &::placeholder {
      color: #d3d3d5;
    }

    &--last {
      margin-bottom: 40px;
    }
  }

  &__checkbox {
    display: none;
  }

  &__checkbox + &__label:before {
    content: "";
    position: absolute;
    top: 9px;
    left: 0;
    margin: -5px 0 0 0;
    height: 24px;
    width: 24px;
    font-size: 20px;
    line-height: 22px;
    border-radius: 2px;
    background-color: #ffffff;
  }

  &__checkbox:checked + &__label:before {
    background-color: #2bba53;
    background-image: url("../img/check.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &__row {
    display: flex;
  }
}
