.slider {

  &__container {
    width: 100%;
    height: 100%;
  }

  &__content {
    padding-right: 30px;
  }

  &__toggles {
    display: flex;
    justify-content: space-between;
    align-items: center;
    order: 1;
    width: 100%;
    height: 48px;
    padding: 0 10px;

    margin: 0;
    background-color: hsl(218, 6%, 26%);
  }

  &__toggle {
    width: 20px;
    height: 16px;
    padding: 24px 20px;
    margin: -10px;

    border: 0;
    background-color: transparent;
    background-image: url("../img/arrow-right.svg");
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;

    &--prev {
      transform: rotate(180deg);
    }
  }

  &__counts {
    padding-top: 15px;
    padding-bottom: 10px;
    color: #f5f5f6;
  }
}
