.button {
  display: inline-block;
  width: 100%;
  padding: 17px 0 11px 0;

  font-size: 22px;
  line-height: 31px;
  text-align: center;
  color: #ffffff;

  border-radius: 30px;
  background-color: #2bba53;
  cursor: pointer;

  &__feedback {
    position: fixed;
    bottom: 15px;
    z-index: 100;

    width: 92%;
  }

  @media (min-width: 768px) {
    &__feedback {
      right: 24px;
      width: 330px;
    }
  }
}
