.page-header {
  background-color: #eeeeee;

  &__inner {
    position: relative;
    min-height: 684px;

    padding-top: 18px;

    background-image: url("../img/background-header-mobile@1x.png");
    background-repeat: no-repeat;
    background-position: bottom 10px center;
    background-size: 100%;
  }

  &__logo img {
    border-radius: 50%;
  }

  &__phone {
    position: absolute;
    top: 9px;
    right: 10px;
    margin: 0;

    font-weight: 700;

    & a {
      display: block;
      padding: 10px;
      color: #000000;
    }

  }

  &__lead {
    margin: 0;
    margin-bottom: 27px;
    padding-top: 46px;

    font-size: 45px;
    line-height: 51px;
    font-weight: 900;
  }

  &__desc {
    margin: 0;

    font-size: 24px;
    line-height: 32px;
  }

  @media (min-width: 768px) {
    &__inner {
      padding-top: 30px;

      background-image: url("../img/background-header-tablet@1x.png");
      background-size: auto;
      background-position: bottom;
    }

    &__lead {
      margin-bottom: 34px;
      padding-top: 73px;
      padding-right: 112px;
    }

    &__phone {
      right: 14px;
    }
  }
}
