.tabs {

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 4px;
  }

  &__link {
    padding: 12px 10px 10px;
    font-family: inherit;
    font-size: 16px;
    line-height: 22px;
    background-color: #ffffff;
    border: none;

    & span {
      border-bottom: 1px dotted;
    }
  }

  &__item--active &__link {
    background-color: #eeeeee;
    border: none;

    & span {
      border: none;
    }
  }

  &__content {
    display: none;
    padding-top: 30px;

    font-size: 18px;

    &--active {
      display: block;
    }
  }
}
