.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 101;
  display: none;
  min-width: 320px;

  &--show {
    display: block;
  }

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 80px 15px 50px;
    overflow: scroll;
  }

  &__feedback {
    color: #f5f5f6;
    background-color: #2b2c30;

    &-title {
      margin: 0;
      margin-bottom: 20px;

      font-size: 30px;
      line-height: 42px;
      font-weight: 800;
    }

    @media (width: 320px) {
      &-title {
        padding-right: 10px;
      }
    }

    &-desc {
      margin: 0;
      margin-bottom: 50px;

      font-size: 20px;
      line-height: 29px;
    }
  }

  &__wrap-button {
    position: relative;
    margin: 0;
  }

  &__close {
    position: absolute;
    top: 3px;
    right: 0px;
    z-index: 2;

    &--icon {
      width: 60px;
      height: 60px;
      padding: 0;
      font-size: 0;
      background: none;
      border: 0;
    }

      &:before, &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 32px;
        height: 3px;
        background-color: #d3d3d5;
        transform-origin: 50% 50%;
      }

      &:before {
        transform: translateX(-50%) rotate3d(0, 0, 1, 45deg);
      }

      &:after {
        transform: translateX(-50%) rotate3d(0, 0, 1, -45deg);
      }
    }
}

@media (min-height: 660px) {
  .modal__feedback-button {
    position: absolute;
    bottom: 50px;

    width: 93%;
  }
}
