.routes {

  &__list li:last-child {
    margin-bottom: 0;
  }

  &__item {
    position: relative;
    text-indent: 25px;
    margin: 0;
    margin-bottom: 25px;

    &:after {
      content: '';
      position: absolute;
      top: 3px;
      left: 0;
      width: 15px;
      height: 15px;
      border-radius: 50%;
    }

    &--grey:after {
      background-color: #acadaf;
    }

    &--orange:after {
      background-color: #fbaa33;
    }
  }
}
