.contacts {
  padding-top: 40px;
  padding-bottom: 50px;

  font-size: 20px;

  &__heading {
    margin-bottom: 16px;
  }

  &__subheading {
    margin-bottom: 12px;
  }

  &__links {
    margin: 0;
    margin-bottom: 10px;

    & a {
      font-size: 24px;
      line-height: 34px;
      color: #000000;
    }
  }

  &__work-hours {
    margin: 0;
    margin-bottom: 16px;
    padding-bottom: 16px;

    border-bottom: 1px solid #cdcdcd;
  }

  &__chat {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
  }

  &__chat-link {
    display: inline-block;
    width: 177px;
    height: 50px;
    background-color: #25d366;
    background-image: url("../img/logo-whatsapp.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &__taxi-call {
    position: relative;
    margin-bottom: 25px;
    padding: 60px 15px 30px;

    line-height: 28px;

    border-radius: 4px;
    background-color: #ffeB81;

    &:before {
      content: '';
      position: absolute;
      top: 20px;
      left: 15px;
      width: 62px;
      height: 25px;
      background-image: url("../img/icon-taxi.svg");
      background-repeat: no-repeat;
    }
  }

  &__map {
    width: 100%;
    margin-bottom: 30px;
  }
}
